.portfolio {
  padding-top: 40px;
  padding-bottom: 40px;

  &__header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    padding-left: 40px;
    padding-right: 40px;

    @media (max-width: 900px) {
      padding-bottom: 20px;
      padding-left: 0;
      padding-right: 0;
    }

    & h2 {
      margin: 0;
      padding-bottom: 40px;
      color: $primary-color;
      font-family: $mainFontExtraBold;
      font-weight: 800;
      font-size: 48px;
      line-height: 100%;
      text-transform: uppercase;

      @media (max-width: 1280px) {
        font-size: 32px;
        line-height: 100%;
      }

      @media (max-width: 600px) {
        padding-bottom: 10px;
        font-size: 24px;
        line-height: 100%;
      }
    }
  }
  &__link {
    display: inline-block;
    vertical-align: middle;
    padding-right: 40px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 23px;

    color: $primary-color;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.5s;

    background-image: url(../img/icons/icon-ar-right.svg);
    background-repeat: no-repeat;
    background-position: 95% 50%;
    background-size: contain;

    &:focus,
    &:hover {
      transition: 0.5s;
      background-position: 100% 50%;
    }
  }

  &__footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    @media (max-width: 600px) {
      flex-direction: column;
    }
  }

  &__btn {
    padding: 18px 62px;
    margin: 0 20px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 23px;
    color: #ffffff;
    background-color: $primary-color;
    border: 1px solid $primary-color;
    text-transform: uppercase;
    border-radius: 0;
    outline: none;
    transition: 0.5s;

    cursor: pointer;

    @media (max-width: 1280px) {
      padding: 11px 32px;
    }

    @media (max-width: 800px) {
      flex-grow: 1;
      margin-left: 10px;
      margin-right: 10px;
      margin-bottom: 20px;
      padding: 10px;
    }

    @media (max-width: 767px) {
      margin-left: 0;
      margin-right: 0;
      width: 100%;
    }

    &:focus,
    &:hover {
      transition: 0.5s;
      opacity: 0.5;
    }

    &--white {
      color: $primary-color;
      background-color: #fff;
    }
  }

  &__slider {
    padding-left: 40px;
    padding-right: 40px;

    @media (max-width: 900px) {
      padding-left: 0;
      padding-right: 0;
    }

    & .swiper-wrapper {
      padding-bottom: 60px;
    }

    & .swiper-pagination {
      display: none;

      @media (max-width: 900px) {
        display: flex;
        justify-content: center;
        padding-bottom: 20px;
      }
    }

    &.swiper-container-horizontal
      > .swiper-pagination-bullets
      .swiper-pagination-bullet {
      margin: 0 20px;
    }

    & .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      background-color: #f2f2f2;
    }

    & .swiper-pagination-bullet-active {
      background-color: #a89365;
    }

    & .swiper-button-next,
    & .swiper-button-prev {
      position: absolute;
      top: 25%;
      right: 0;
      left: auto;
      transform: translateY(-25%);
      @media (max-width: 900px) {
        display: none;
      }
    }

    & .swiper-button-prev {
      left: 0;
    }

    & .swiper-button-next::after,
    & .swiper-button-prev::after {
      color: $primary-color;
      font-size: 1.5rem;
    }
  }
}
