html {
  height: 100%;
}

.overlay {
  overflow: hidden;
  height: 100vh;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

h1 {
  @include adaptiv-font(50, 34);
}

h2 {
  @include adaptiv-font(45, 24);
}

h3 {
  @include adaptiv-font(35, 24);
}

h4,
h5,
h6,
p {
  font-size: 1em;
}

img {
  width: 100%;
  max-width: 100%;
  height: 100%;
  object-fit: contain;
}

