.product {
  transition: 0.5s;

  & img {
    display: inline-block;
    max-width: 100%;
    height: auto;
    object-fit: contain;
    padding-bottom: 20px;
  }

  & h3 {
    margin: 0;
    padding-bottom: 20px;
    font-family: $mainFontBold;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 100%;
    color: $primary-color;

    @media (max-width: 1280px) {
      font-size: 24px;
      line-height: 100%;
    }
  }

  & p {
    margin: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;

    @media (max-width: 1280px) {
      font-size: 16px;
      line-height: 24px;
    }
  }
}
